import React from "react";
import Card from 'react-bootstrap/Card'
import Horaire from "./Horaire";

const Etablissement = (props) => {
    return (
        <>
           <Card border="primary" className="card card-body flex-fill">
                <Card.Header style={{backgroundColor: '#0080FF'}} className="text-center text-white p-3">{props.nom}</Card.Header>
                <Card.Body>
                <Card.Title variant="secondary">
                   Telephone : {props.telephone} <br /><br />
                   {props.email && `Mail : ${props.email}`} <br />
                </Card.Title>
                <Card.Text>
                    <strong>Adresse : </strong>
                    {props.adresse[0].lignes[0]} {' '}
                    {props.adresse[0].codePostal} - {props.adresse[0].commune}
                    <br />
                    <strong>Horaires : </strong> <br />
                    {props.horaires && <Horaire horaires= {props.horaires}/>}

                </Card.Text>
                 {props.url && <Card.Link href={props.url} target="_blank">Visiter le site web</Card.Link>}
                </Card.Body>
            </Card>
        </>
    )

}
export default Etablissement;