import React, { Component } from "react";
import {Switch, Route} from 'react-router-dom';
import NavBar from "./component/NavBar/NavBar";
import Accueil from "./container/Accueil";
import Contact from "./container/Contact";
import Localisation from "./container/Localisation";
import Container from 'react-bootstrap/Container';
import Route404 from './component/route404/Route404';
import Footer from './component/footer/Footer';


class Site extends Component {

    render(){
        return (
            <>
                <div className="site">
                    <NavBar />
                    <Container>
                        <Switch>
                            <Route path="/" exact render={() => <Accueil />}/>
                            <Route path="/localisation" exact render={() => <Localisation />}/>
                            <Route path="/contact" exact render={(props) => <Contact {...props}/>}/>
                            <Route render={() => <Route404 />}/>
                        </Switch>
                    </Container>
                </div>
                <div className="monSite"></div>
                <Footer />
            </>
        )
    }

}
export default Site;