import React from "react";

const Horaire = (props) => {
    let content = props.horaires.map((horaire, index) => {
        if(horaire.du === horaire.au){
            return <li key={index}>
                {horaire.du}  - {horaire.heures[0].de} à {horaire.heures[0].a}
            </li>
        }
        return <li key={index}>
                    Du {horaire.du} au {horaire.au} - {horaire.heures[0].de} à {horaire.heures[0].a}
                </li>
    })

    return (
        <>
            {content}
        </>
    )

}
export default Horaire;