import React, {Component} from "react";
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Badge from 'react-bootstrap/Badge';
import { withFormik } from 'formik';
import * as Yup from 'yup';
import axios from 'axios';
import CONSTANTES from "../Constantes/Constantes";

class ContactForm extends Component {
    state = {
        nom:'',
        email:'',
        message:''
    }

    handleVisible = () => { 
        this.props.setStatus({success : true});
        setTimeout(() => { 
            this.props.setStatus({success : false});
            this.props.formulaireContact();
        }, 3000);
    } 

    render(){
        return(
            <>
                <Form>
                    <Form.Group controlId="nom">
                        <Form.Label>Nom :</Form.Label> <span><Badge variant="warning">(min 5 caractères)</Badge></span>
                        <Form.Control 
                            type="text" 
                            placeholder="Votre nom" 
                            onChange={this.props.handleChange}
                            onBlur={this.props.handleBlur} 
                            value={this.props.values.nom}
                            name="nom"
                        />
                        {
                            this.props.touched.nom && this.props.errors.nom && 
                            <span className="text-danger">
                                {this.props.errors.nom}
                            </span>
                        }
                    </Form.Group>
                    <Form.Group controlId="email">
                        <Form.Label>Email : </Form.Label>
                        <Form.Control 
                            type="text" 
                            placeholder="Email" 
                            onChange={this.props.handleChange}
                            onBlur={this.props.handleBlur} 
                            value={this.props.values.email}
                            name="email"
                        />
                        {
                            this.props.touched.email && this.props.errors.email && 
                            <span className="text-danger">
                                {this.props.errors.email}
                            </span>
                        }
                    </Form.Group>
                    <Form.Group controlId="message">
                        <Form.Label>Votre message: </Form.Label> <span><Badge variant="warning">(min entre 100 et 200 caractères)</Badge></span>
                        <Form.Control 
                            as="textarea" 
                            rows={6} 
                            onChange={this.props.handleChange}
                            onBlur={this.props.handleBlur} 
                            value={this.props.values.message}
                            name="message"
                        />
                        {
                            this.props.touched.message && this.props.errors.message && 
                            <span className="text-danger">
                                {this.props.errors.message}
                            </span>
                        }
                    </Form.Group>
                    
                    <Button variant="primary" onClick={() => this.props.handleSubmit()} className="bouton mb-2">
                        Envoyer
                    </Button>
                </Form>
            </>
        )
    }
}

export default withFormik({
    mapPropsToValues: () => ({ 
        nom: '',
        email:'',
        message:''
    }),
    
    // Custom sync validation
    validationSchema: Yup.object().shape({
        nom: Yup.string()
                .min(5, 'Le nom doit être de 5 caractère minimum')
                .required("Le nom est obligatoire"),
        email: Yup.string()
                  .email('L\'email n\'est pas au bon format')
                  .required("L'email est obligatoire"),
        message: Yup.string()
                .min(100, 'Le message doit être de 100 caractère minimum')
                .max(200, 'Le message ne doit pas dépasser 200 caractères')
                .required("Le message est obligatoire")
    }),
    
    handleSubmit: (values, props) => {
        console.log("on est dans submit", props, values)
        const payload = {
            nom: values.nom,
            email: values.email,
            message: values.message
        }
        axios.post(CONSTANTES.URL_FIREBASE, payload).then(response => {
            props.setStatus({success : true});
            props.props.formulaireContact();
            props.props.handleVisible(values.nom, values.email, values.message);
            // Object.keys(values).forEach(key => (values[key] = ""));
            // props.resetForm(values);
        })
        .catch(error => {
            props.setStatus({success : false});
            props.setSubmitting(false);
            props.setErrors({ submit: error.message });
        })
    },
    
    displayName: 'BasicForm',
    }
)(ContactForm);