import React, {Component} from "react";
import Titre from '../component/Titre/titre';
import Button from 'react-bootstrap/Button';
import ContactForm from '../component/Contact/ContactForm';
import AlertDismissible from "../component/alert/alert";

class Contact extends Component {
    state = {
        formulaireContacte: false,
        afterMessage: false,
        sendMessage : {
            nom:"",
            email:"",
            message:""
        }
    }

    handleFormulaireContact = () => {
        this.setState({formulaireContacte: !this.state.formulaireContacte});
    }

    handleVisible = (nom, email, message) => { 

        const sendMessage = {
            nom: nom,
            email: email,
            message: message
        }
        
        this.setState({
            afterMessage : true,
            sendMessage: sendMessage
        }, () => {
            console.log("le nom est ", nom, email, message)
        });
        setTimeout(() => { 
            this.setState({afterMessage : false});
        }, 5000);
    } 

    render(){
        return(
            <>
               <Titre>Vous pouvez nous contacter</Titre>
               <div>
                   <h2>Adresse :</h2>
                   <p>
                       1 Rue de Paris, 75015 Paris
                   </p>
               </div>
               <div>
                   <h2>Téléphone :</h2>
                   <p>
                       01 35 43 23 67
                   </p>
               </div>
               <div className="flex">
                    <h2>Vous préfèrez nous écrire ? {' '}</h2>
                    <Button variant="primary" onClick={() => this.handleFormulaireContact()}>Formulaire de Contact</Button>
               </div>
               <div>
                   {
                       this.state.formulaireContacte && <ContactForm handleVisible={this.handleVisible} formulaireContact={this.handleFormulaireContact}/>
                   }
               </div>
               <div>
                   {this.state.afterMessage && <AlertDismissible  sendMessage={this.state.sendMessage}/>}
               </div>
            </>
        )
    }
}

export default Contact;