import React, {Component} from "react";
import Titre from "../component/Titre/titre";
import Image from 'react-bootstrap/Image';
import Chateau from '../asset/chateau.jpg';

class Accueil extends Component {
    render(){
        return(
            <>
               <Titre>Bienvenu sur Gouv</Titre>
               <p>
                   Le site vous aidant à trouver les établissement publics de la France
               </p>
               <Image src={Chateau} fluid  thumbnail style={{width:"100%"}}/>
            </>
        )
    }
}

export default Accueil;