import React, { Component } from "react";
import Alert from 'react-bootstrap/Alert';

class AlertDismissible extends Component {
    render(){
        return (
            <>
                <Alert variant="success" className="m-2">
                    <Alert.Heading>
                        <strong>Votre nom </strong>: {this.props.sendMessage.nom}<br />
                        <strong>Votre email</strong> : {this.props.sendMessage.email}
                    </Alert.Heading>
                    <hr />
                    <p className="mb-0">
                       Votre message : {this.props.sendMessage.message}
                    </p>
                </Alert>
            </>
        )
    }
}
export default AlertDismissible;