import React, {Component} from "react";
import axios from 'axios';
import CONSTANTES from '../component/Constantes/Constantes'
import Titre from "../component/Titre/titre";
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form'
import Etablissement from "../component/Etablissement/etablissement";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col'

class Localisation extends Component {
    state = {
        recherches : null,
        departement: null,
        type: null
    }

    handleChoixDepartement = (e) => {
        this.setState({departement : e.target.value}, () => {
            console.log(this.state.departement);
        });
    }

    handleRechercheEtablissement = (type) => {
        const url = CONSTANTES.BASE_URL + CONSTANTES.BASE_ENDPOINT + this.state.departement + '/' + type;
        axios.get(url).then(response => {
            console.log(response)
            this.setState({recherches : response.data.features, type : type}, () => {
                console.log(this.state.recherches)
            })
        }).catch(error => {
            console.log(error)
        })
    }

    render(){
        return(
            <>
               <Titre>Rechercher un établissement</Titre>
               <div>
                    <Form>
                        <Form.Group controlId="selectMultiple" className="select">
                            <Form.Label className="formLabel"><strong>Choix du département : </strong></Form.Label>
                            <Form.Control as="select" custom onChange={(e) => this.setState({departement : e.target.value})} className="formControl">
                                <option>Choisir un département</option>
                                <option value="93">Département de Seine-Saint-Denis (93)</option>
                                <option value="75">Département de Paris (75)</option>
                                <option value="94">Val-de-Marne (94)</option>
                                <option value="92">Haut-de-Seine (92)</option>
                                <option value="77">Seine-Et-Marne (77)</option>
                                <option value="91">Essonne (91)</option>
                                <option value="78">Yvelines (78)</option>
                                <option value="95">Val d'Oise (95)</option>
                                <option value="09">Ariège (09)</option>
                            </Form.Control>
                        </Form.Group>
                    </Form>
               </div>
               <div>
                   {
                       this.state.departement && (
                        <>
                            <div className="boutonLocalisation">
                                <Button variant="secondary" onClick={() => this.handleRechercheEtablissement('mairie')}>Mairie</Button>{' '}
                                <Button variant="secondary" onClick={() => this.handleRechercheEtablissement('commissariat_police')}>Commissariat de police</Button>{' '}
                                <Button variant="secondary" onClick={() => this.handleRechercheEtablissement('gendarmerie')}>Gendarmerie </Button>{' '}
                                <Button variant="secondary" onClick={() => this.handleRechercheEtablissement('prefecture ')}>Préfecture</Button>{' '}
                                <Button variant="secondary" onClick={() => this.handleRechercheEtablissement('pole_emploi')}>Pôle Emploi</Button>{' '}
                                <Button variant="secondary" onClick={() => this.handleRechercheEtablissement('defenseur_droits')}>Défenseur des droits</Button>{' '}
                            </div>
                            <Row className="row">
                            {
                                this.state.recherches && this.state.recherches.length === 0 ? (
                                    <h1>Aucune Information de {this.state.type}  pour le departement {this.state.departement}</h1>
                                ) :(
                                    this.state.recherches && this.state.recherches.map((recherche, index) =>{
                                        return (
                                            <Col key={recherche.properties.id} md={6} className="col col-sm d-flex">
                                                <Etablissement 
                                                        nom={recherche.properties.nom}
                                                        adresse={recherche.properties.adresses}
                                                        horaires={recherche.properties.horaires}
                                                        email={recherche.properties.email}
                                                        telephone={recherche.properties.telephone}
                                                        id={recherche.properties.id}
                                                        url={recherche.properties.url}
                                                    />
                                            </Col>
                                            )
                                    })
                                )
                            }
                            </Row>
                        </>
                       )                
                   }
               
                </div>
            </>
        )
    }
}

export default Localisation;